@import "ScssHelpers/helpers-all.scss";

.sort_by_mobile {
	position: absolute;
	top: 100%;
	left: 0;

	width: 100%;
	height: auto;

	padding: 2.4rem 0;

	background-color: $white;
	opacity: 0;
	transform: translateY(-3rem);
	pointer-events: none;

	z-index: 5;

	transition: opacity 0.3s ease, transform 0.3s ease;

	.filters_product_list__form--sort_state & {
		opacity: 1;
		transform: translateY(0);
		pointer-events: unset;
	}
}

.sort_by_mobile__top {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: flex-start;

	padding-bottom: 1.8rem;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;

		width: 100%;
		height: 1px;

		background-color: $gold_2;
	}

	&:not(:last-child) {
		margin-bottom: 2.2rem;
	}
}

.sort_by_mobile__title {
	font-size: 1.4rem;
	color: $gray_2;
	font-weight: 500;
	letter-spacing: 0.1em;
	line-height: 1.2;

	text-transform: uppercase;

	&:not(:last-child) {
		margin-right: 1.4rem;
	}
}

.sort_by_mobile__icon_close {
	position: relative;
	z-index: 5;

	width: 2.2rem;
	height: 1.6rem;
	margin-left: auto;

	overflow: hidden;
	cursor: pointer;

	&::before,
	&::after {
		@include transition_transform;

		content: "";
		position: absolute;
		top: 50%;
		right: 0;

		width: 100%;
		height: 1px;

		background-color: $gold_2;
	}

	&::before {
		transform: translateY(-0.5rem);
		transform: rotate(-45deg);
	}

	&::after {
		transform: translateY(0.5rem);
		transform: rotate(45deg);
	}
}

.sort_by_mobile__wrap {
}

.sort_by_mobile__list {
}

.sort_by_mobile__item {
	position: relative;

	// padding: 0.6rem 0;

	&:not(:last-child) {
		margin-bottom: 0.4rem;
	}

	&.sort_by_mobile__item--active_mod {
		&::after {
			content: "";
			position: absolute;
			left: 50%;
			top: 0;

			width: 105%;
			height: 100%;

			transform: translateX(-50%);

			background-color: $beige_1;
		}

		&::before {
			content: "";
			position: absolute;
			right: 1rem;
			top: 50%;

			z-index: 5;

			display: block;
			width: 0.8rem;
			height: 1.4rem;
			border: 2px solid $gold_2;
			border-top: 0;
			border-left: 0;
			transform: rotate(45deg) translate(-58%, -58%);
		}
	}
}

.sort_by_mobile__link {
	position: relative;

	z-index: 5;
	font-size: 1.6rem;
	color: $black;
	letter-spacing: -0.01em;
	line-height: 1.5;
}
