@import "ScssHelpers/helpers-all.scss";

.filter_mobile {
	@include bp($point_2, min) {
		display: none;
	}
	@include bp($point_3) {
		position: fixed;
		top: 0;
		left: 0;
		width: calc(100% - 2rem);
		height: 100%;

		z-index: 101;

		background-color: $beige_1;

		transform: translateX(-100%);
		overflow-y: auto;

		transition: transform 0.3s ease;

		.filters_product_list__form--filter_state & {
			transform: translateX(0);
		}
	}
}

.filter_mobile__close {
	padding: 2.2rem var(--gl-indent) 2.6rem;

	background-color: $white;
}

.filter_mobile__close_icon {
	position: relative;
	z-index: 5;

	width: 2.2rem;
	height: 1.6rem;

	overflow: hidden;
	cursor: pointer;

	&::before,
	&::after {
		@include transition_transform;

		content: "";
		position: absolute;
		top: 50%;
		right: 0;

		width: 100%;
		height: 1px;

		background-color: $gold_2;
	}

	&::before {
		transform: translateY(-0.5rem);
		transform: rotate(-45deg);
	}

	&::after {
		transform: translateY(0.5rem);
		transform: rotate(45deg);
	}
}

.filter_mobile__wrap {
	padding: 3.6rem var(--gl-indent);
}

.filter_mobile__top {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: flex-start;

	width: 100%;
	padding-bottom: 1rem;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;

		width: 100%;
		height: 2px;

		background-color: $gray_12;
	}
}

.filter_mobile__heading {
	color: $gray_2;
	font-size: 1.4rem;
	font-weight: 500;

	letter-spacing: 0.1em;
	line-height: 1.2;
	text-transform: uppercase;
	cursor: pointer;

	&:not(:last-child) {
		margin-right: 2rem;
	}
}

.filter_mobile__clear {
	position: relative;

	margin-left: auto;
	padding-right: 1.4rem;

	color: $gold_2;
	font-size: 1.1rem;
	font-weight: 500;

	letter-spacing: 0.09em;
	line-height: 1.2;
	text-transform: uppercase;

	&::before,
	&::after {
		@include transition_transform;

		content: "";
		position: absolute;
		top: 50%;
		right: 0;

		width: 1rem;
		height: 1px;

		background-color: $gold_2;
	}

	&::before {
		transform: translateY(-0.5rem);
		transform: rotate(-45deg);
	}

	&::after {
		transform: translateY(0.5rem);
		transform: rotate(45deg);
	}
}

.filter_mobile__in {
	&:not(:last-child) {
		margin-bottom: 0.6rem;
	}
}

.filter_mobile__list {
}

.filter_mobile__item {
	position: relative;

	padding-top: 1.8rem;
	padding-bottom: 2.4rem;

	&:not(:last-child) {
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;
			height: 2px;

			background-color: $gray_12;
		}
	}
}

.filter_mobile__head {
	display: flex;
	align-items: center;
	justify-content: flex-start;

	cursor: pointer;
	transition: opacity 0.3s ease;

	&:focus,
	&:hover {
		opacity: 0.7;
	}
}

.filter_mobile__item_title {
	font-size: 1.8rem;
	font-weight: 500;

	line-height: 1.33;
	letter-spacing: -0.012em;

	&:not(:last-child) {
		margin-right: 1.4rem;
	}
}

.filter_mobile__item_icon {
	flex-shrink: 0;

	margin-left: auto;

	color: $gold_2;
	transition: transform 0.3s ease, color 0.3s ease;

	.filter_mobile__item--active_state & {
		transform: rotate(-180deg);
	}

	@include bp($point_2, min) {
		width: 3.2rem;
		height: 1.6rem;
		margin: 0.7rem 1px 0 auto;
	}
	@include bp($point_3) {
		width: 1.8rem;
		height: 0.8rem;
	}
}

.filter_mobile__body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.filter_mobile__item--active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.filter_mobile__sublist {
	padding: 2.2rem 0 1.8rem;
}
.filter_mobile__sublist_item {
	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.filter_mobile__btn {
	width: 100%;
}
