@import "ScssHelpers/helpers-all.scss";

.filters_product_list__title {
	@include h3;

	letter-spacing: -0.016em;

	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.filters_product_list {
	position: relative;

	&:not(:last-child) {
		margin-bottom: 5.6rem;
	}

	.wher_buy_info &:not(:last-child) {
		margin-bottom: 6.4rem;
	}

	.news_list &:not(:last-child) {
		padding-bottom: 5.6rem;
		margin-bottom: 4.8rem;

		border-bottom: 1px solid #f2e4d1;
	}
}

.filters_product_list__form {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;

	&:not(:last-child) {
		margin-right: 2rem;
	}
}

.filters_product_list__count {
	@include text;

	align-self: end;
	margin-left: auto;
	padding: 0 0.5rem 1.7rem 0;

	line-height: 1.5;

	@include bp($point_4 - 1) {
		display: none;
	}
}

.filters_product_list__items {
	@include flex_block(row, wrap, flex-start, flex-start);

	flex-shrink: 0;
	width: 75%;

	margin: 0 -2.4rem -2.4rem 0;

	@include bp($point_4 - 1) {
		display: none;
	}
}

.filters_product_list__item {
	flex-shrink: 0;
	width: 33.333%;

	// align-self: end;
	margin-top: auto;

	padding: 0 2.4rem 2.4rem 0;
}

.filters_product_list__search {
	margin-left: auto;

	width: 25%;

	// .filters_product_list--color_mod & input {
	// 	box-shadow: inset 0 0 0 1px $gold_2;
	// }

	@include bp($point_2, min) {
		padding-top: 3.4rem;
	}
	@include bp($point_3) {
		padding-top: 2.4rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.filters_product_list__btn {
	position: absolute;
	right: 0;
	top: 0;

	width: 3.8rem;
	height: 100%;
	padding: 1rem 1.2rem 1.4rem 0.2rem;

	cursor: pointer;
}

.filters_product_list__icon {
	@include cover_img;

	color: $gold_2;
}

//=================================================

.filters_product_list__mobile {
	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		position: relative;
		display: flex;
		align-items: center;

		width: 100vw;

		padding: 0.9rem 0;

		background-color: $beige_1;

		&::after,
		&::before {
			content: "";
			position: absolute;
			top: 0;

			height: 100%;
			width: 3rem;

			background-color: $beige_1;
		}

		&::after {
			left: -3rem;
		}
		&::before {
			right: -3rem;
		}
	}
}

.filters_product_list__mobile_item {
	display: flex;
	align-items: center;
	justify-content: flex-start;

	&:nth-child(3n + 1),
	&:nth-child(3n + 3) {
		width: 25%;
	}

	&:nth-child(3n + 2) {
		width: 50%;
		justify-content: center;
	}
}

.filters_product_list__mobile_wrap {
	position: relative;
	z-index: 6;

	display: flex;
	align-items: center;

	.filters_product_list__mobile_item--1_mod & {
		margin: 0 auto;
	}

	.filters_product_list__mobile_item--2_mod & {
		margin-left: auto;
		margin-right: -1rem;
	}
	cursor: pointer;
}

.filters_product_list__mobile_title {
	font-size: 1.4rem;
	font-weight: 500;
	line-height: 1.7;
	letter-spacing: -0.03em;

	&:not(:last-child) {
		margin-right: 0.5rem;
	}
}

.filters_product_list__mobile_filter {
	width: 1.8rem;
	height: 1.8rem;
	flex-shrink: 0;

	color: $gold_2;

	&:not(:last-child) {
		margin-right: 0.6rem;
	}
}

.filters_product_list__mobile_arrow {
	width: 1rem;
	height: 0.5rem;

	color: $gold_2;

	transition: transform 0.3s ease;

	.filters_product_list__form--sort_state & {
		transform: rotate(-180deg);
	}
}

.filters_product_list__mobile_icon {
	width: 3.8rem;
	height: 3.8rem;

	padding: 1rem;

	color: $gold_2;
	border-radius: 50%;
	opacity: 0.8;

	transition: background-color 0.3s ease, opacity 0.3s ease;
	cursor: pointer;

	&:not(:last-child) {
		margin-left: auto;
	}

	&.filters_product_list__mobile_icon--active_state {
		background-color: $white;
		opacity: 1;
	}
}

.filters_product_list__mobile_count {
	margin-left: auto;

	font-size: 1.4rem;
	letter-spacing: -0.036em;
	line-height: 1.5;
}
